.main-visual__scroll {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #4282B8;
  font-family: "Zen Kaku Gothic New", sans-serif;
}

/* スクロールダウンの位置 */
.scroll {
  position: absolute;
  bottom: 1.1875rem;
}
@media screen and (min-width: 768px) {
  .scroll {
    z-index: 100;
    margin-left: -1rem;
  }
}

/* 線のアニメーション部分 */
.scroll::before {
  animation: scroll 2s infinite;
  background-color: #4282B8;
  content: "";
  bottom: -3.25rem;
  height: 3.25rem;
  left: 0;
  margin: auto;
  position: absolute;
  right: 0;
  width: 1px;
}
@media screen and (min-width: 768px) {
  .scroll::before {
    bottom: -7rem;
    height: 7rem;
  }
}

/* 線のアニメーション */
@keyframes scroll {
  0% {
    transform: scale(1, 0);
    transform-origin: 0 0;
  }
  50% {
    transform: scale(1, 1);
    transform-origin: 0 0;
  }
  51% {
    transform: scale(1, 1);
    transform-origin: 0 100%;
  }
  100% {
    transform: scale(1, 0);
    transform-origin: 0 100%;
  }
}
.scroll__text {
  letter-spacing: 0.04em;
  margin-left: 0.5625rem;
  margin-bottom: 0.5rem;
}

